import React, { Component } from 'react';
import { Badge, Card, CardBody, CardHeader, Col, Pagination, PaginationItem, PaginationLink, Row, Table } from 'reactstrap';
import { Button, Input, InputGroup, InputGroupAddon, InputGroupText } from 'reactstrap';
import { CardGroup, Container, Form, FormGroup, Fade, Alert, Label } from 'reactstrap'
import { Modal, ModalBody, ModalFooter, ModalHeader, ButtonDropdown, DropdownItem, DropdownMenu, DropdownToggle } from 'reactstrap'
import { shareDecrypt, doMessage } from '../eshop'
import { postSafe, sign } from '../../ggv/js/gegeUtil'


class Debug extends Component {

  constructor(props) {
    super(props);
    this.state = { data: '', url: 'https://www.btbresource.com:19099/bshop', response: 'res' }
  }

  updateSecret(e) {
    if (e && e.target) {
      let data = e.target.value;
      this.setState({ data })
    }
  }

  updateUrl(e) {
    if (e && e.target) {
      let url = e.target.value;
      this.setState({ url })
    }
  }

  doSend() {
    let { url } = this.state;
    //url = "https://192.168.195.134:19099/bshop"
    try {
      let data = JSON.parse(this.state.data);
      postSafe(url, data, (ok, res) => {
        console.log(ok + ":" + res)
        let response = ok + ":" + res;
        this.setState({ response })
      })
    } catch (e) {
      let response = e.toString();
      this.setState({ response })
    }
  }

  render() {
    return (
      <Card>
        <CardBody>
          <FormGroup>
            <Input type="text" name="textarea-input" id="textar"
              placeholder={this.state.url} onChange={(this.updateUrl).bind(this)} />
          </FormGroup>
          <FormGroup>
            <Input type="textarea" name="textarea-input" id="textarea-input" rows="5"
              placeholder='data' onChange={(this.updateSecret).bind(this)} />
          </FormGroup>
          <FormGroup>
            <Input type="textarea" name="textarea-input" id="textarea-input" rows="5"
              value={this.state.response}
            ></Input>
          </FormGroup>
          <FormGroup>
            <Button color="danger" onClick={() => {
              this.doSend()
            }} size='sm'>/bshop</Button>
          </FormGroup>
        </CardBody>
      </Card>
    );
  }
}

export default Debug;
